import React from 'react';

import Button from '../../components/button';
const ButtonGroup = Button.Group;

export default () => (
  <div id='main-container'>
    <h1 className='h1'></h1>
    <Button type='normal'></Button>
    <h1 className='h1'></h1>
    <Button type='primary'></Button>
    <Button></Button>
    <Button disabled></Button>
    <Button type='dashed'></Button>
    <Button type='danger'></Button>
    <Button icon='tag'></Button>
    <Button icon='plus-circle-o' text></Button>
    <h1 className='h1'></h1>
    <Button type='create' icon='plus' />
    <Button type='quit' icon='logout' />
    <h1 className='h1'></h1>
    <div className='youling'>
      <Button ghost></Button>
      <Button type='primary' ghost></Button>
      <Button type='dashed' ghost></Button>
      <Button type='danger' ghost></Button>
    </div>
    <h1 className='h1'></h1>
    <ButtonGroup>
      <Button></Button>
      <Button type='primary'></Button>
    </ButtonGroup><br />
  </div>
	)



