import React from 'react';

import Input from 'components/input';
import Icon from 'components/icon';

// const InputGroup = Input.Group
const Textarea = Input.Textarea
const Search = Input.Search

export default () => (
  <div id='main-container'>
    <h1 className='h1'></h1>
    <Input type='text' placeholder='Basic usage' />
    <h1 className='h1'></h1>
    <Input type='text' addonBefore='' placeholder='' addonAfter='' />
    <h1 className='h1'>search</h1>
    <Search style={{ width: 200 }} enterButton='search' onSearch={(value => console.log(value))} />
    <h1 className='h1'></h1>
    <Input placeholder='Enter your name' prefix={<Icon type='user' />} suffix={<Icon type='user' />} onChange={(e) => console.log(e.target.value)} />
    <h1 className='h1'></h1>
    <Textarea />
    <h1 className='h1'>(2-6)</h1>
    <Textarea autosize={{minRows: 2, maxRows: 6}} />
  </div>
	)



