import React from 'react'
import { Layout, Sider, Footer, Header, Content } from 'components/Layout'

import './layout.css'

const MainLayout = () => (
  <div id='main-container'>
    <h1 className='h1'>1</h1>
    <div className='layout_temp'>
      <Layout>
        <Sider />
        <Layout>
          <Header />
          <Content />
          <Footer />
        </Layout>
      </Layout>
    </div>
    <br />
    <h1 className='h1'>2</h1>
    <div className='layout_temp'>
      <Layout>
        <Header />
        <Content />
        <Footer />
      </Layout>
    </div>
    <br />
    <h1 className='h1'>3 </h1>
    <div className='layout_temp'>
      <Layout>
        <Sider toogle={1} />
        <Layout>
          <Header />
          <Content />
          <Footer />
        </Layout>
      </Layout>
    </div>
  </div>
)

export default MainLayout;



