import React from 'react';
import { Modal } from 'components/modal/index';
import Button from 'components/button/button';
const confirm = Modal.confirm;

export default class ModalView extends React.Component {
  state = {
    visible: false,
    asyncvisible: false,
    ModalText: '',
    loading: false,
    footervisible: false,
    modal1Visible: false,
    modal2Visible: false
   }

  showModal = () => {
    this.setState({
      visible: true
    });
  }

  handleOk = (e) => {
    console.log('');
    this.setState({
      visible: false
    });
  }

  handleCancel = (e) => {
    this.setState({
      visible: false
    });
  }

  // 
  asyncshowModal = () => {
    this.setState({
      asyncvisible: true
    });
  }
  asynchandleOk = (e) => {
    this.setState({
      ModalText: '',
      confirmLoading: true
    });
    setTimeout(() => {
      this.setState({
        asyncvisible: false,
        confirmLoading: false
      });
    }, 2000);
  }
  asynchandleCancel = (e) => {
    console.log('');
    this.setState({
      asyncvisible: false
    });
  }

  // 
  footershowModal = () => {
    this.setState({
      footervisible: true
    });
  }
  footerhandleOk = () => {
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({ loading: false, footervisible: false });
    }, 3000);
  }
  footerhandleCancel = () => {
    this.setState({ footervisible: false });
  }

  // 
  info = () => {
    Modal.info({
      title: '',
      content: '',
      onOk: function() {},
    });
  }

  success = () => {
    Modal.success({
      title: '',
      content: ''
    });
  }

  error = () => {
    Modal.error({
      title: '',
      content: ''
    });
  }

  // 
  showConfirm = () => {
    confirm({
      iconType: 'close-circle',
      title: '',
      content: '',
      onOk: function() {
        console.log('');
      },
      onCancel: function() {}
    });
  }

  // ()
  autosuccess = () => {
    const modal = Modal.success({
      title: '',
      content: ''
    });
    setTimeout(() => modal.destroy(), 1000);
  }

  // 
  setModal1Visible = (modal1Visible) => {
    this.setState({ modal1Visible });
  }
  setModal2Visible = (modal2Visible) => {
    this.setState({ modal2Visible });
  }

  render() {
    return (
      <div id='main-container'>
        <div className='h1'>
        ()
        </div>
        <Button type='primary' onClick={this.showModal}></Button>
        <Modal title=' Modal' visible={this.state.visible}
          onOk={this.handleOk} onCancel={this.handleCancel} width={800}>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </Modal>

        {/*  */}
        <div className='h1'>
        
        </div>
        <Button type='primary' onClick={this.asyncshowModal}></Button>
        <Modal title=' Modal' visible={this.state.asyncvisible}
          onOk={this.asynchandleOk} onCancel={this.asynchandleCancel}>
          <p>{this.state.ModalText}</p>
        </Modal>

        {/*  */}
        <div className='h1'>
          
        </div>
        <Button type='primary' onClick={this.footershowModal}>
        
        </Button>
        <Modal ref='modal'
          visible={this.state.footervisible}
          title='' onOk={this.footerhandleOk} onCancel={this.footerhandleCancel}
          footer={[
            <Button key='back' type='ghost' size='small' onClick={this.footerhandleCancel}> </Button>,
            <Button key='submit' type='primary' size='small' loading={this.state.loading} onClick={this.footerhandleOk}>
             
            </Button>
        ]}>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </Modal>

        {/*  */}
        <div className='h1'>
        
        </div>
        <Button onClick={this.info}></Button>
        <Button onClick={this.success}></Button>
        <Button onClick={this.error}></Button>

        {/*  */}
        <div className='h1'>
        
        </div>
        <Button onClick={this.showConfirm}>
        
        </Button>

        {/*  () */}
        <div className='h1'>
        
        </div>
        <Button onClick={this.autosuccess}></Button>

        {/*  */}
        <div className='h1'>
        
        </div>
        <Button type='primary' onClick={() => this.setModal1Visible(true)}> 20px </Button>
        <Modal
          title=' 20px '
          style={{ top: 20 }}
          visible={this.state.modal1Visible}
          onOk={() => this.setModal1Visible(false)}
          onCancel={() => this.setModal1Visible(false)}
          footer={[
            <Button key='cancel' type='ghost' size='small' onClick={() => this.setModal1Visible(false)}> </Button>,
            <Button key='ok' type='primary' size='small' onClick={() => this.setModal1Visible(false)}></Button>
          ]}>
          <p></p>
          <p></p>
          <p></p>
        </Modal>
        <Button type='primary' onClick={() => this.setModal2Visible(true)}></Button>
        <Modal
          title=''
          wrapClassName='vertical-center-modal'
          visible={this.state.modal2Visible}
          onOk={() => this.setModal2Visible(false)}
          onCancel={() => this.setModal2Visible(false)}
          footer={[
            <Button key='cancel' type='ghost' size='small' onClick={() => this.setModal2Visible(false)}> </Button>,
            <Button key='ok' type='primary' size='small' onClick={() => this.setModal2Visible(false)}></Button>
          ]}
        >
          <p></p>
          <p></p>
          <p></p>
        </Modal>

      </div>
    )
  }
};



